import { Component, OnInit, Input, ViewChild, Inject, inject } from '@angular/core';
import { Dish } from '../shared/dish';
import { Router, Params ,ActivatedRoute} from '@angular/router';
import { Location } from '@angular/common';
import { DishService } from '../services/dish.service';
import {  switchMap } from 'rxjs/operators';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Comment } from '../shared/comment';
import { MatSliderModule} from '@angular/material/slider';
import { visibility,flyInOut,expand } from '../animations/app.animation';
@Component({
  selector: 'app-dishdetail',
  templateUrl: './dishdetail.component.html',
  styleUrls: ['./dishdetail.component.scss'],
  host:{
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations:[
    flyInOut(),
    visibility(),
    expand()
  ]
})
export class DishdetailComponent implements OnInit {
  /*@Input()
  //no longer needed*/
  dish: Dish;
  dishIds: string[];
  prev: string;
  next: string;
  errMss: string;
  dishCopy: Dish;
  visibility='shown';
  
  //form properties:
  @ViewChild('cform') commentFormDirective;
  commentForm : FormGroup;
  comment : Comment;

  formErrors = {
    'rating': '',
    'comment': '',
    'author': '',
  };

  validationMessages = {
    'author': {
      'required': 'Name is required!',
      'minlength': 'Name should be at least 2 characters'
    },
    'rating':{
      'required': 'rating is required!'
    },
    'comment': {
      'required': 'Please add your comments'
    }
  };

  constructor(private dishservice: DishService,
    private route: ActivatedRoute,
    private location: Location,
    public router:Router,
    private fb: FormBuilder,
    @Inject('BaseURL') public BaseURL ) { 
    this.createForm();
  }

  ngOnInit(): void {
    this.dishservice.getDishIds()
      .subscribe(dids => this.dishIds=dids);
    this.route.params
      .pipe(switchMap((params: Params) => {
        this.visibility='hidden';
        return this.dishservice.getDish(params['id']);
        }))
      .subscribe(dish=>{
         this.dish = dish;
         this.dishCopy=dish;
         this.setPrevNext(dish.id);
         this.visibility='shown';},
        errmsg => this.errMss=<any>errmsg);
  }

  setPrevNext(dishId: string) {
    const index = this.dishIds.indexOf(dishId);
    this.prev = this.dishIds[(this.dishIds.length+index-1) % this.dishIds.length];
    this.next = this.dishIds[(this.dishIds.length+index+1) % this.dishIds.length];
  }

  createForm() {
    this.commentForm = this.fb.group({        
        author : ['', [Validators.required, Validators.minLength(2)] ],
        rating : ['', Validators.required],
        comment : ['', Validators.required]
      });

    this.commentForm.valueChanges
      .subscribe(data => this.onValueChange(data));
    this.onValueChange();
  }

  onValueChange(data?: any) {
    if( !this.commentForm ){ return; }
    const form = this.commentForm;
    for(const field in this.formErrors) {
      if(this.formErrors.hasOwnProperty(field)) {
        this.formErrors[field] = '';
        const control = form.get(field);
        if( control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if( control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  onSubmit() {
    this.comment= this.commentForm.value;
    console.log(this.comment);
    this.commentForm.reset({
      author:"",
      comment:"",
      rating: "",
      //date: ""
    });
    this.comment['date']= new Date().toISOString();
    this.dishCopy.comments.push(this.comment);
    this.dishservice.putDish(this.dishCopy)
      .subscribe(dish => {
        this.dish= dish;
        this.dishCopy = dish;
      }, 
      err => { this.dish=null;this.dishCopy=null;this.errMss= <any>err});
    this.commentFormDirective.resetForm();
  }

  onLike(){
  	console.log("Thank you for liking "+this.dish.name);
  }

  goBack():void {
    this.router.navigate(['/menu']);//go('/menu');
  }

  getDishid():string {
    return this.dish.id;
  }
}
